<template>
    <div class="index">
        <a-spin tip="..." :spinning="spinning" size="large">
            <a-layout>
                <a-layout-header class="header-part">
                    <a-row>
                        <a-col :span="2">
                            <div class="logo"></div>
                        </a-col>
                        <a-col :span="9">
                            <div class="customerInformation">
                                <p>:{{ this.customerName }}</p>
                                <p>:{{ this.projectName }}</p>
                            </div>
                        </a-col>
                    </a-row>
                </a-layout-header>
                <a-layout-content>
                    <fileListPage
                        ref="uploadPage"
                        :data="menuList"
                        :fileList="fileList"
                        @transferFile="transferFile"
                    />
                </a-layout-content>
            </a-layout>
        </a-spin>
    </div>
</template>
<script>
import fileListPage from '../components/fileListPage.vue';
import session from '../data/session/sessionUtils';
import { setCookie } from '../libs/utils/cookieUtils';
export default {
    name: 'index',
    components: {
        fileListPage,
    },
    data() {
        return {
            menuList: [],
            fileList: [],
            customerData: {},
            customerName: '',
            projectName: '',
            spinning: false,
            fileCollection: [],
        };
    },
    methods: {
        transferFile(value) {
            let target = value.target || {};
            let moveItem = value.moveItem || {};
            let targetInformation = value.target.extraOptions || {};

            //!workpaperId: , workpaperId:
            if (target.extraOptions.workpaperId) {
                this.checkIfExitAttach(
                    target,
                    moveItem,
                    targetInformation,
                    null
                );
            } else {
                let value = 'AUDITEVIDENCE';
                if (value.value === 'AUDITEVIDENCE') {
                    // - 
                    this.checkIfExitFile(
                        target,
                        moveItem,
                        targetInformation,
                        value
                    );
                } else {
                    this.uploadTempFile(
                        target,
                        moveItem,
                        targetInformation,
                        value
                    );
                }
            }
        },
        fileTransfer(target, moveItem, targetInformation, path, data) {
            //
            this.spinning = true;
            let obj = {
                param1: targetInformation.projectId || null,
                param2: targetInformation.customerId || null,
                param3: moveItem.title || null,
                param4: targetInformation.autoId || null,
                param5: moveItem.sourceFileId || null,
                param6: targetInformation.nodeType || null,
                param7: path,
                jsonData: JSON.stringify(data),
            };
            this.$sacpApis
                .fileTransfer(JSON.parse(JSON.stringify(obj)))
                .then((res) => {
                    if (res.success) {
                        this.$message.success(' !');
                        this.getPartSubjecttree();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        uploadTempFile(target, moveItem, targetInformation, value) {
            this.spinning = true;
            let params = {
                param1: JSON.stringify(moveItem), //id
            };
            this.$sacpApis
                .uploadTempFile(JSON.parse(JSON.stringify(params)))
                .then((res) => {
                    if (res.success) {
                        let path = res.data[0].path || null;
                        value === 'AUDITEVIDENCE'
                            ? this.fileTransfer(
                                  target,
                                  moveItem,
                                  targetInformation,
                                  path,
                                  res.data
                              )
                            : this.attachTransfer(
                                  target,
                                  moveItem,
                                  targetInformation,
                                  path,
                                  res.data
                              );
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        checkIfExitFile(target, moveItem, targetInformation) {
            // 
            this.spinning = true;
            let params = {
                param1: targetInformation.customerId, //customerId,
                param2: targetInformation.projectId, //projectId,
                param3: moveItem.title, //fileName
                param4: targetInformation.autoId, //autoId
            };
            this.$sacpApis
                .checkIfExitFile(JSON.parse(JSON.stringify(params)))
                .then((res) => {
                    if (res.success) {
                        // true -    false - 
                        if (!res.data[0].flag)
                            return this.fileTransfer(
                                target,
                                moveItem,
                                targetInformation
                            );
                        let that = this;
                        this.$confirm({
                            title: `${moveItem.title}`,
                            onOk() {
                                that.uploadTempFile(
                                    target,
                                    moveItem,
                                    targetInformation,
                                    'AUDITEVIDENCE'
                                );
                            },
                            onCancel() {
                                that.spinning = false;
                            },
                        });
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.spinning = false;
                });
        },
        checkIfExitAttach(target, moveItem, targetInformation) {
            // 
            this.spinning = true;
            let params = {
                param1: targetInformation.customerId, //customerId,
                param2: targetInformation.projectId, //projectId,
                // param3: moveItem.title, //fileName
                param4: targetInformation.workpaperId, //workpaperId
                jsonData: JSON.stringify(moveItem),
            };
            this.$sacpApis
                .checkIfExitAttach(JSON.parse(JSON.stringify(params)))
                .then((res) => {
                    if (res.success) {
                        // true -    false - 
                        let flieList = [];
                        res.data.forEach((x) => {
                            if (x.flag === true) {
                                flieList.push(x);
                            }
                        });

                        if (!flieList.length) {
                            return this.uploadTempFile(
                                target,
                                moveItem,
                                targetInformation,
                                null
                            );
                        }

                        this.$confirm({
                            title: `${flieList.join(
                                ';'
                            )}`,
                            onOk: () => {
                                this.uploadTempFile(
                                    target,
                                    moveItem,
                                    targetInformation,
                                    null
                                );
                                // that.attachTransfer(target,moveItem,targetInformation)
                            },
                            onCancel: () => {
                                this.spinning = false;
                            },
                        });
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.spinning = false;
                });
        },
        attachTransfer(target, moveItem, targetInformation, path, data) {
            this.spinning = true;
            let obj = {
                param1: targetInformation.projectId, //projectId,
                param2: targetInformation.customerId, //customerId,
                param3: moveItem.title, //fileName
                param4: targetInformation.autoId, //sourceFileId
                param5: moveItem.sourceFileId, //id
                param6: targetInformation.indexId, //
                param7: targetInformation.workpaperId, //id
                param8: path,
                jsonData: JSON.stringify(data),
            };
            this.$sacpApis
                .attachTransfer(obj)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('!');
                        this.getPartSubjecttree();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        getPartSubjecttree() {
            this.$sacpApis
                .getPartSubjecttree({ param1: this.customerData.curProjectId })
                .then((res) => {
                    if (res.success) {
                        this.menuList = res.data[0].treeData.children;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
        getCustomerInfomation() {
            this.spinning = true;
            this.$sacpApis
                .queryCustomerInformation()
                .then((res) => {
                    if (res.success) {
                        this.customerData = res.data[0];
                        this.customerName = `${this.customerData.curCustomerId}-${this.customerData.curCustomerName}`;
                        this.projectName = this.customerData.curProjectName;
                        session.saveUserInfo(this.customerData);
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.getPartSubjecttree();
                    this.getClientPortalTree();
                    this.spinning = false;
                });
        },

        getClientPortalTree() {
            const origin = window.location.origin;
            const param = {
                param1: this.customerData.curProjectId,
                // param2: '172.16.60.101',
                param3: origin + '/Faith/transfer/index.html',
            };
            this.$sacpApis
                .getClientPortalTree(param)
                .then((res) => {
                    if (res.success) {
                        this.fileList = res.data[0].treeData; /**/
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                });
        },
    },
    created() {
        this.getCustomerInfomation();
        // setCookie(
        //     'SESSION',
        //     'ZjE3YmQxMzctZTY1Yy00NDMwLWJhZmYtMjQwNzEyY2Y4MjYz',
        //     1
        // );
    },
};
</script>


