<template>
    <div class="file-list-page">
        <a-spin tip="..." :spinning="spinning">
            <a-layout ref="main">
                <a-layout-sider class="file-list-menu">
                    <div class="logo">
                        <img src="../assets/images/logo.svg" alt="" /><span
                            ></span
                        >
                    </div>
                    <a-directory-tree
                        :draggable="true"
                        :showIcon="false"
                        :tree-data="menuList"
                        :blockNode="true"
                        :selectedKeys="selectedKeys"
                        :default-expanded-keys="expandedKeys"
                        @drop="(event) => addNewItem(event)"
                        @mouseleave="leave()"
                        @click="(e) => handleSelectMenuItem(e)"
                        @dragover="arrive"
                        @dragleave="fileLeave"
                    />
                </a-layout-sider>
                <a-layout>
                    <a-layout-header>
                        <a-breadcrumb>
                            <a-breadcrumb-item
                                v-for="item in breadcrumbItem"
                                :key="item.key"
                            >
                                <!-- <a-icon type="profile" v-if="item.key == '0'" /> -->
                                <span
                                    v-html="item.title"
                                    @click="handleClickBreadcrumb(item)"
                                />
                            </a-breadcrumb-item>
                        </a-breadcrumb>
                    </a-layout-header>
                    <a-layout-content>
                        <div class="clearfix">
                            <ul class="fileList">
                                <template v-for="(item, idx) in files">
                                    <li
                                        :key="item.index"
                                        v-if="
                                            fileType(item.title)[0].format !=
                                                'file' && idx == 0
                                        "
                                    >
                                        <input
                                            class="checkbox"
                                            type="checkbox"
                                            v-model="checkAll"
                                            @change="onChangeAll"
                                        />
                                        
                                    </li>
                                    <li
                                        :key="item.index"
                                        :draggable="draggable"
                                        @dblclick="folderClick(item)"
                                        @dragstart="dragenter(item)"
                                        id="dragtarget"
                                        style="height: 50px"
                                    >
                                        <!-- <a-icon
                                        theme="twoTone"
                                        :type="fileType(item.title)"
                                    ></a-icon> -->
                                        <input
                                            class="checkbox"
                                            type="checkbox"
                                            v-model="item.checked"
                                            v-if="
                                                fileType(item.title)[0]
                                                    .format != 'file'
                                            "
                                            @change="onChange($event, idx)"
                                        />

                                        <img
                                            :src="fileType(item.title)[0].icon"
                                            alt=""
                                            style="height: 80%"
                                            ondragstart="return false;"
                                        />
                                        <span :title="item.title"
                                            >{{ item.title }}
                                            <a
                                                v-if="
                                                    fileType(item.title)[0]
                                                        .format != 'file'
                                                "
                                                @click="lookPreview(item)"
                                                ></a
                                            >
                                        </span>
                                    </li>
                                </template>
                            </ul>
                        </div>
                    </a-layout-content>
                </a-layout>
            </a-layout>
        </a-spin>
        <a-modal
            title=""
            width="80%"
            :visible="visible"
            :confirm-loading="confirmLoading"
            :footer="false"
            @ok="handleOk"
            @cancel="handleCancel"
        >
            <div style="text-align: center" v-if="preview.isImg">
                <img :src="preview.url" alt="" />
            </div>
            <iframe
                id="iframe"
                :src="preview.url"
                frameborder="0"
                v-else
            ></iframe>
        </a-modal>
    </div>
</template>
<script>
import XEUtils from 'xe-utils';
import { every, indexOf } from 'xe-utils/methods';
import axios from 'axios';
import Sortable from 'sortablejs';

export default {
    name: 'fileListPage',
    props: {
        data: Array,
        fileList: Array,
        topPath: String,
    },
    watch: {
        data: {
            //
            handler(val) {
                val = val || [];
                this.menuList = val;
                if (val && val.length) {
                    this.autoOpenTree();
                }
            },
            immediate: true,
            deep: true,
        },
        fileList: {
            //
            handler(val) {
                val = val || [];
                this.files = val.map((item) => ({
                    children: item.children,
                    key: item.key,
                    title: item.title,
                    scopedSlots: { icon: 'custom' },
                    type: item.type,
                }));
                if (this.files && this.files.length) {
                    this.files[0].children.forEach((x) => {
                        x.checked = false;
                    });
                    this.autoOpenFolder();
                }
            },
            immediate: true,
            deep: true,
        },
    },
    data() {
        return {
            spinning: false,
            collapsed: false,
            menuList: [],
            treeArr: [],
            breadcrumbItem: [{ title: '', key: '0', path: '/index' }],
            selectedKeys: [],
            files: [],
            moveItem: {},
            draggable: false,
            expandedKeys: [],
            visible: false,
            confirmLoading: false,
            preview: { url: '', isImg: false },
            checkAll: false,
        };
    },
    methods: {
        /**/
        handleSelectMenuItem(e) {
            let targetTitle = e.target.innerText;
            let menuTreeArr = XEUtils.toTreeArray(this.menuList);
            let target =
                menuTreeArr.filter((item) => item.title === targetTitle) || [];
            if (target && target.length) {
                this.selectedKeys = [];
                this.selectedKeys.push(target[0].key);
            }
        },
        /*header */
        handleClickBreadcrumb(item) {
            this.breadcrumbItem.map((ele, index) => {
                if (ele.key == item.key) {
                    this.breadcrumbItem.splice(
                        index + 1,
                        this.breadcrumbItem.length - (index + 1)
                    );
                }
            });
            if (item.key === '0') {
                this.files = this.fileList;
                this.draggable = false;
            }
        },
        /**/
        addNewItem(event) {
            console.log('', event);
            if (!this.moveItem.length) return;
            /**/
            let targetTitle = event.node.title || '';
            let targetKey = event.node.eventKey || '';
            // return;
            /*  1.XXXXXXXXX
                3. -  endFlolder:'0'
                4. endFlolder:'1'
            */
            let menuTreeArr = XEUtils.toTreeArray(this.menuList);
            let targetFolder =
                XEUtils.searchTree(
                    this.menuList,
                    (item) => item.key === targetKey
                ) || []; /*tree*/
            let targetFolderTreeArr =
                XEUtils.toTreeArray(targetFolder) ||
                []; /*tree*/

            if (
                targetFolder &&
                targetFolder.length &&
                targetFolderTreeArr &&
                targetFolderTreeArr.length
            ) {
                //extraOptions.nodeType  'FILE'- 
                let target =
                    targetFolderTreeArr.filter(
                        (item) =>
                            item.key === targetKey && item.title === targetTitle
                    )[0] || {}; /**/
                let nodeType = target.extraOptions
                    ? target.extraOptions.nodeType
                    : target.type;
                if (
                    target.endFlolder === '0' &&
                    target.type != 'FILE' &&
                    target.type != 'AUDITEVIDENCE'
                )
                    return this.$message.error(' !');
                //workpaperId,
                if (
                    !target.endFlolder &&
                    target.type === 'FILE' &&
                    !target.extraOptions.workpaperId
                )
                    return this.$message.error(' !');
                /**/
                if (nodeType != 'FILE' && target.endFlolder === '1') {
                    let that = this;
                    if (
                        target.children &&
                        target.children.length &&
                        target.children.some(
                            (item) => item.title === this.moveItem.title
                        )
                    ) {
                        this.$confirm({
                            title: `${this.moveItem.title}`,
                            onOk() {
                                that.$emit('transferFile', {
                                    target: target,
                                    moveItem: that.moveItem,
                                    value: 'cover',
                                });
                            },
                            onCancel() {
                                that.clearData();
                            },
                        });
                    } else {
                        this.$emit('transferFile', {
                            target: target,
                            moveItem: this.moveItem,
                        });
                    }
                } else if (
                    !target.endFlolder &&
                    nodeType === 'FILE' &&
                    target.extraOptions.workpaperId
                ) {
                    /*  - 1. 2.*/
                    this.$emit('transferFile', {
                        target: target,
                        moveItem: this.moveItem,
                    });
                } else if (
                    target.endFlolder === '0' &&
                    target.type == 'AUDITEVIDENCE'
                ) {
                    // - 
                    this.$emit('transferFile', {
                        target: target,
                        moveItem: this.moveItem,
                        value: 'AUDITEVIDENCE',
                    });
                }
            }
        },
        clearData() {
            this.moveItem = {};
            this.active = false;
        },
        leave() {
            this.clearData();
        },
        folderClick(item) {
            //
            if (item.type === 'FILE') return;
            if (item.children && item.children.length) {
                let breadcrumbItemCache = this.breadcrumbItem.filter(
                    (item) => item.key == '0'
                );
                this.breadcrumbItem = [];
                this.breadcrumbItem.unshift(breadcrumbItemCache[0]);
                this.breadcrumbItem.push(item);
                this.files = item.children;
                this.draggable = true;
            }
        },
        dragenter(item) {
            if (Array.isArray(this.files)) {
                this.moveItem = this.files.filter((x) => x.checked);
                this.moveItem.forEach((x) => {
                    x.fileName = x.title;
                });
            } else {
                this.moveItem = [];
            }
            // if (item.type === 'FILE') {
            //     this.moveItem = item;
            // }
        },
        arrive(e) {
            e.node.$el.style.backgroundColor = '#efeeec';
        },
        fileLeave(e) {
            e.node.$el.style.backgroundColor = '';
        },
        // 
        autoOpenTree() {
            let query = location.search;
            if (!query) return;
            const param = JSON.parse(`${decodeURI(query.split('=')[1])}`);
            if (param) {
                const name = param.nodeName;
                const menuTree = XEUtils.searchTree(
                    this.menuList,
                    (item) => item.title == name
                );
                const menuArr = XEUtils.toTreeArray(menuTree);
                menuArr.forEach((el) => {
                    this.expandedKeys.push(el.key);
                });
            }
        },
        // 
        autoOpenFolder() {
            let query = location.search;
            if (!query) return;
            const param = JSON.parse(`${decodeURI(query.split('=')[1])}`);
            if (param) {
                const name = param.userSubjectName;
                console.log('name', name);
                const file = XEUtils.searchTree(
                    this.files,
                    (item) => item.title == name
                );
                if (file && file.length) {
                    this.folderClick(file[0]);
                }
            }
        },
        fileType(title) {
            // console.log('title', title);
            const fileTitle = `${title.split('.').pop()}`.toLocaleLowerCase();
            const word = require('../assets/images/word.svg');
            const excel = require('../assets/images/Microsoft-Excel.svg');
            const pdf = require('../assets/images/pdf.svg');
            const ppt = require('../assets/images/PPT.svg');
            const video = require('../assets/images/video.svg');
            const img = require('../assets/images/file_img.svg');
            const file = require('../assets/images/file.svg');
            const fileList = [
                { format: 'doc', icon: word },
                { format: 'png', icon: img, type: 'img' },
                { format: 'xlsx', icon: excel },
                { format: 'pdf', icon: pdf },
                { format: 'docx', icon: word },
                { format: 'bmp', icon: img, type: 'img' },
                { format: 'jpg', icon: img, type: 'img' },
                { format: 'png', icon: img, type: 'img' },
                { format: 'tif', icon: img, type: 'img' },
                { format: 'gif', icon: img, type: 'img' },
                { format: 'pcx', icon: img, type: 'img' },
                { format: 'tga', icon: img, type: 'img' },
                { format: 'exif', icon: img, type: 'img' },
                { format: 'fpx', icon: img, type: 'img' },
                { format: 'svg', icon: img, type: 'img' },
                { format: 'psd', icon: img, type: 'img' },
                { format: 'cdr', icon: img, type: 'img' },
                { format: 'pcd', icon: img, type: 'img' },
                { format: 'dxf', icon: img, type: 'img' },
                { format: 'ufo', icon: img, type: 'img' },
                { format: 'eps', icon: img, type: 'img' },
                { format: 'ai', icon: img, type: 'img' },
                { format: 'raw', icon: img, type: 'img' },
                { format: 'wmf', icon: img, type: 'img' },
                { format: 'webp', icon: img, type: 'img' },
                { format: 'avif', icon: img, type: 'img' },
                { format: 'mpeg/mpg/dat', icon: video },
                { format: 'avi', icon: video },
                { format: 'mov', icon: video },
                { format: 'asf', icon: video },
                { format: 'wmv', icon: video },
                { format: 'navi', icon: video },
                { format: '3gp', icon: video },
                { format: 'real video', icon: video },
                { format: 'mkv', icon: video },
                { format: 'flv', icon: video },
                { format: 'f4v', icon: video },
                { format: 'rmvb', icon: video },
                { format: 'webm', icon: video },
                { format: 'hddvd', icon: video },
                { format: 'qsv', icon: video },
                { format: 'mp4', icon: video },
            ];
            const type = [];
            for (var i = 0; i < fileList.length; i++) {
                if (fileTitle.indexOf(fileList[i].format) > -1) {
                    type.push(fileList[i]);
                }
            }

            if (!type.length) {
                type.push({ format: 'file', icon: file });
            }

            // console.log('fileType', type);
            return type;
        },
        lookPreview(item) {
            this.visible = true;
            if (this.fileType(item.title)[0].type === 'img') {
                const index = item.fileLink.search('archive');
                const origin = window.location.origin;
                const uat = 'https://sacpuat.bdo.com.cn';
                const formal = 'https://sacp.bdo.com.cn';
                this.preview = {
                    url:
                        origin === formal
                            ? formal
                            : uat + '/portal/img/' + item.fileLink.slice(index),
                    isImg: true,
                };
            } else {
                this.preview = {
                    url:
                        'https://preview.bdoportal.cn/onlinePreviewSecret?url=' +
                        item.securityLink,
                    isImg: false,
                };
                this.$nextTick(() => {
                    const iframe = document.getElementById('iframe');
                    iframe.style.width = '100%';
                    iframe.style.height = 'calc(100vh - 250px)';
                });
            }
        },
        handleOk(e) {
            this.confirmLoading = true;
            setTimeout(() => {
                this.visible = false;
                this.confirmLoading = false;
            }, 2000);
        },
        handleCancel(e) {
            this.visible = false;
        },
        onChangeAll(e) {
            this.checkAll = e.target.checked;
            this.files.forEach((x) => {
                x.checked = e.target.checked;
            });
            this.$forceUpdate();
        },
        onChange(e, idx) {
            this.files[idx].checked = e.target.checked;
            if (!e.target.checked) {
                this.checkAll = false;
            } else {
                this.checkAll = this.files.every((x) => x.checked === true);
            }
        },
    },
    mounted() {
        // this.fileType();
    },
};
</script>
<style lang="scss" scoped>
.file-list-page {
    height: 100%;
    .ant-layout-header {
        width: 100%;
        -webkit-box-shadow: 0 0 5px 2px #dcdfe6;
        box-shadow: 0 0 5px 2px #dcdfe6;
        height: auto !important;
        min-height: 30px !important;
        background: #fff !important;
        line-height: 30px !important;
        padding: 0 !important;
    }
    .ant-layout-sider {
        background-color: white !important;
        border-right: 1px solid #dcdcdc;
        height: 100%;
        overflow: auto;
        width: 300px !important;
        max-width: 300px !important;
        flex: 0 0 300px !important;

        &::-webkit-scrollbar {
            width: 5px;
            height: 5px;
            background-color: #f5f5f5;
        }

        &::-webkit-scrollbar-thumb {
            background: #d8d8d8;
            border-radius: 5px;
        }

        .logo {
            display: flex;
            align-items: center;
            height: 60px;

            img {
                height: 100%;
            }
            span {
                font-size: 22px;
                color: #409eff;
            }
        }
    }
    .ant-tree {
        border-top: 1px solid #dcdcdc;
        li {
            cursor: pointer;
            .ant-tree-title:hover {
                color: white !important;
            }
            span[draggable],
            span[draggable='true'] {
                border-top: 0 !important;
                border-bottom: 0 !important;
            }
            span {
                font-size: 16px;
                line-height: 24px !important;
                color: gray !important;
            }

            .ant-tree-node-content-wrapper::before {
                padding-top: 20px !important;
                background-color: unset !important;
            }
        }

        .ant-tree-directory {
            & > li {
                span {
                    .ant-tree-node-content-wrapper {
                        .ant-tree-node-selected {
                            .ant-tree-title {
                                padding: 3px !important;
                            }
                        }
                        &:hover::before {
                            background-color: #ecebeb !important;
                            /* color: white !important; */
                        }
                    }
                }
            }
        }
    }
    /**/
    .ant-layout-header {
        border-bottom: 1px solid #dcdcdc;
        .ant-breadcrumb {
            margin-left: 8px;
            height: 60px;
            line-height: 60px;
            font-size: 16px;
            // margin: 5px 0;
            padding-left: 20px;
            .ant-breadcrumb-link {
                span {
                    line-height: 30px;
                    cursor: pointer;
                }
            }
        }
    }
    /**/
    .ant-layout-content {
        background: #fff !important;
        .clearfix {
            ul {
                display: flex;
                flex-direction: column;
                margin-left: 40px;

                li {
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;
                    border-bottom: 1px solid #dcdcdc;
                    padding-left: 20px;
                    height: 50px;
                    cursor: pointer;
                    .checkbox {
                        margin-right: 10px;
                        cursor: pointer;
                        width: 15px;
                        height: 15px;
                        &:hover {
                            border-color: #409eff;
                        }
                    }
                    image {
                        height: 80%;
                    }
                    &:hover {
                        // border: 3px solid #1ed0ff;
                        background-color: #efeeec;
                    }
                    .anticon {
                        font-size: 55px;
                        margin: 8px 0;
                    }
                    span {
                        // overflow-wrap: anywhere;
                        overflow: hidden;
                        word-break: keep-all;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        padding: 0 5px;
                    }
                }
            }
        }
    }
}
</style>


